@import url(fonts.css);

:root {
    --content-gradient: linear-gradient(rgba(49, 15, 173, 0.75), rgba(6, 38, 109, 0.75));
    --background-overlay: linear-gradient(rgba(49, 15, 173, 0.25), rgba(6, 38, 109, 0.25));
    --accent-font: 'Quadrangle';
    --main-font: 'Courier New', Courier, monospace;
    --letter-color: #f2f2f2;
    --background-image: url("../img/pyramids.png");
    --accent-color: rgba(102, 58, 223, 0.75);
    --accent-gradient: linear-gradient(rgba(102, 58, 223, 0.75),rgba(39, 19, 150, 0.75));
    --table-border-color: #add2bd;
    --table-header-color: #18452694;
    --list-prefix: "üê† ";
}

body{
    background: #ffffff var(--background-image) no-repeat right top;
    background-size: cover;
    color: var(--letter-color);
    font-family: var(--main-font);
    overflow-y: hidden;
}

#root{
    width: 96vw;
    margin: auto;
    /* background-image: var(--background-overlay); */
    height: 100vh;
}

header{
    margin-top: 2vh;
}

main{
    display: flex;
    height: 75vh;
    margin-top: 2vh;
}

.content{
    background-color: rgba(128, 128, 128, 0.80);
}

.center-image{
    display: block;
    margin-left: auto;
    margin-right: auto;
}