@import url("base.css");

#navbar{
    background-image: var(--content-gradient);
    display: flex;
    justify-content:space-between;
    width: 70vw;
    margin: auto;
    font-family: var(--accent-font);
    font-size: x-large;
    border-radius: 1vw;
}

.header-container:first-child:hover{
    border-radius: 1vw 0 0 1vw;
}

.header-container:last-child:hover{
    border-radius: 0 1vw 1vw 0;
}

.header-container{
    padding: 1vw;
    color: unset;
    text-decoration: none;
    width: 6vw;
    text-align: center;
    flex: 1;
}

.header-dropdown{
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding: 12px 16px;
}

.header-container:hover .header-dropdown{
    display: block;
}

.header-container:hover{
    background-image: var(--accent-gradient);
    box-shadow: 0px 0px 1vw rgb(191, 180, 248);
}

#main-logo{
    display: flex;
    justify-content: center;
}

