@import url(base.css);

#main-content{
    margin: 0 3vw 2vh;
    width: 55vw;
    height: 100%;
    border-radius: 1vw;
    background-color: var(--accent-color);
    overflow-y: scroll;
}

.page-head {
    background-image: var(--content-gradient);
    font-family: var(--accent-font);
    font-size: x-large;
    margin: 0;
    padding: .1vh 1vw;
    border-radius: 2vw 2vw 100% 100%;
    box-shadow: 0 2vh 1vh rgba(0, 0, 0, 0.2);
    text-align: center;
}

.page-content{
    margin: auto;
    margin-top: 5vh;
    margin-bottom: 5vh;
    width: 95%;
}

@media (max-width: 768px){
    #main-content{
        width: 100%;
    }
}