@import url(base.css);

.side-column{
    width: 20vw;
    height: 65vh;
    background-image: var(--content-gradient);
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow-y: scroll;
    border-radius: 1vw;
}

.navbar-entry{
    text-decoration: none;
    color: unset;
    margin: 0;
    padding: 0 0 0 1vw;
}

.navbar-entry:first-child{
    padding-top: 2vh;
}

.navbar-entry p{
    margin-top: 1vh;
    margin-bottom: 1vh;
}

.link:hover{
    background-image: var(--accent-gradient);
    box-shadow: 0px 0px 1vw rgb(191, 180, 248);
}

#sidebar-logo img{
    width: 16vw;
    margin-top: 2vh;
    margin-bottom: 2vh;
}

#button-container{
    margin: 2vh auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}